.default {
    cursor: pointer;
    border: 1px solid #cecece;
    background: #676464;
    box-shadow: inset 0px 20px 20px #ffffff;
    border-radius: 8px;
    width: 60px;
    margin-top: 3px;
}

.default:hover {
    box-shadow: inset 0px -20px 20px #ffffff;
    transform: scale(1.1);
}

.default:active {
    transform: scale(0.9)
}

.default:focus {
    outline: none;
}

.red {
    cursor: pointer;
    border: 1px solid #cecece;
    background: #d23f3f;
    box-shadow: inset 0px 20px 20px #ffffff;
    border-radius: 8px;
    width: 60px;
    margin-top: 3px;
}

.red:hover {
    box-shadow: inset 0px -20px 20px #ffffff;
    transform: scale(1.1);
}

.red:active {
    transform: scale(0.9)
}

.red:focus {
    outline: none;
}