.screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #0414a7;

}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 100px;
}

.error {
    font: normal 40px VT323, monospace;
    border: 1px solid #aaa;
    background-color: #aaa;
    width: 300px;
    text-align: center;
    color: #0414a7
}

.text {
    font: normal 20px/1.25rem VT323, monospace;
    color: #aaa;
}

.ul {
    font: normal 20px/1.25rem VT323, monospace;
    color: #aaa;
}


